import React, { useContext } from 'react';
import { useLocation, useParams } from 'react-router';

import { ROUTES } from '../../utils/routes';
import Forms from '../forms/Forms';
import FormProvider from '../../context/FormContext';
import { CityContext } from '../../context/CityContext';
import { useAppSelector } from '../../hooks/redux';
import { getDataCity, getDataCityClubsTariff } from '../../utils';
import { useSearchCitiesQuery } from '../../store/reducers/shop.api';

export default function FormOrderTariff() {
  const { id } = useParams();
  const location = useLocation();
  const pageCity = useContext(CityContext);
  const { isLoading, isError, data } = useSearchCitiesQuery(pageCity);
  const dataCityActive =
    !isError && !isLoading && !!data && getDataCity(data, pageCity);
  const clubs = dataCityActive?.clubs;
  const dataClubsProducts = dataCityActive?.clubs.map((club: any) => {
    return club.products.filter(
      (el: any) => el.product_type !== 'subscription_recurrent',
    );
  })[0];
  const tariffData: any = [];
  dataClubsProducts &&
    dataClubsProducts?.map((el: any, i: number) =>
      tariffData.push({
        id: i + 1,
        type: el.product_type,
        period: el.product_type,
        title: el.product_name,
        price: el.product_price,
        clubs: [...getDataCityClubsTariff(clubs, el.product_type)],
      }),
    );

  const item = () => tariffData?.find((item: any) => item.id === Number(id));

  const dataItem = item();

  return (
    <div>
      <FormProvider>
        <Forms item={dataItem} />
      </FormProvider>
    </div>
  );
}
