import React, { useContext } from 'react';
import { Scroll } from '../icons';
import CarouselBaseSwiper from '../carousel-base-swiper/CarouselBaseSwiper';
import { BREAKPOINTS } from '../tariffs/data/constants';
import { SwiperSlide } from 'swiper/react';
import { Scrollbar } from 'swiper/modules';

import { CityContext } from '../../context/CityContext';
import TariffItem from '../tariff-item/TariffItem';

import 'swiper/css/scrollbar';
import { useAppSelector } from '../../hooks/redux';
import { getDataCity, getDataCityClubsTariff } from '../../utils';
import { useSearchCitiesQuery } from '../../store/reducers/shop.api';

const carouselProps = {
  slidesPerView: 3,
  spaceBetween: 3,
  modules: [Scrollbar],
};

export default function TariffsCarousel() {
  const pageCity = useContext(CityContext);
  const { isLoading, isError, data } = useSearchCitiesQuery(pageCity);
  const dataCityActive =
    !isError && !isLoading && !!data && getDataCity(data, pageCity);

  const clubs = !isError && !isLoading && !!data && dataCityActive.clubs;
  const dataClubsProducts =
    !isError &&
    !isLoading &&
    !!data &&
    clubs.map((club: any) => {
      return club.products.filter(
        (el: any) => el.product_type !== 'subscription_recurrent',
      );
    })[0];

  const tariffData: any = [];
  dataClubsProducts &&
    dataClubsProducts?.map((el: any, i: number) =>
      tariffData.push({
        id: i + 1,
        type: el.product_type,
        period: el.product_type,
        title: el.product_name,
        price: el.product_price,
        clubs: [...getDataCityClubsTariff(clubs, el.product_type)],
      }),
    );

  return (
    <>
      <Scroll className="xl:hidden w-8 h-8 absolute top-5 right-0 text-white-50" />
      <CarouselBaseSwiper
        wrapperClass="tariffs-slider pb-6"
        data={tariffData}
        sliderId="tariffs-slider"
        breakpoints={BREAKPOINTS}
        count={tariffData?.length}
        {...carouselProps}
      >
        {tariffData.map((el: any) => (
          <SwiperSlide key={el.id} className="">
            <TariffItem data={el} />
          </SwiperSlide>
        ))}
      </CarouselBaseSwiper>
    </>
  );
}
