import { ROUTES } from './routes';

export const BASE_URL =
  process.env.NODE_ENV === 'production'
    ? process.env.REACT_APP_GITHUB_HOMEPAGE
    : process.env.NODE_ENV === 'dev'
      ? process.env.REACT_APP_GITHUB_HOMEPAGE
      : process.env.NODE_ENV === 'development'
        ? process.env.REACT_APP_GITHUB_HOMEPAGE
        : '';

